.prices {
  .container {
    padding-top: 26px;
    padding-left: 4.4%;
  }
}

.prices__header {
  font-weight: 600;
  font-size: 24px;
  line-height: 150%;
  text-align: center;

  color: $dark;
}

.prices__text {
  font-size: 16px;
  line-height: 170%;
  text-align: center;

  color: $grey-text;
}

.prices__contact {
  margin-bottom: 10px;

  font-weight: 500;
  font-size: 18px;
  line-height: 34px;
  text-align: center;

  color: $black;
}

.prices__tel {
  display: block;
  margin-bottom: 39px;

  font-size: 18px;
  line-height: 32px;
  text-align: center;
  text-decoration-line: underline;

  color: $red;
}

.prices__tariff {
  margin: 0;
  margin-bottom: 9px;

  font-weight: 600;
  font-size: 24px;
  line-height: 34px;
  text-align: center;
  letter-spacing: -1.27273px;

  transition: 0.5s;
}

.prices__price {
  margin: 0;
  margin-left: -10px;

  font-weight: bold;
  font-size: 25px;
  line-height: 34px;
  text-align: center;

  transition: 0.5s;
}

.prices__desc {
  margin-top: 12px;
  margin-bottom: 8px;

  font-size: 16px;
  line-height: 24px;
  text-align: center;

  color: $dark;
}

.prices__list {
  padding-top: 22px;
  margin-bottom: 28px;
}

.prices__item {
  margin: 0 auto;
  margin-bottom: 10px;
  padding-left: 30px;

  font-size: 16px;
  line-height: 32px;

  color: $dark;

  &--top {
    margin-bottom: 6px;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.prices__buy {
  display: block;
  width: 117.2%;
  margin-left: -8.6%;
  margin-bottom: -1px;
  padding-top: 21px;
  padding-bottom: 17px;

  text-align: center;
  font-weight: 500;
  font-size: 10px;
  line-height: 12px;
  letter-spacing: 2px;
  text-transform: uppercase;

  color: white;
  background-color: $red;
}

.prices__card {
  padding-top: 31px;
  padding-left: 7%;
  padding-right: 7%;
  margin-bottom: 30px;

  box-sizing: border-box;
  border: 1px solid $grey-button;
  border-radius: 6px;

  &:hover {
    padding-top: 29px;
    padding-left: calc(7% - 2px);
    padding-right: calc(7% - 2px);
    border: 3px solid $red;
    border-bottom: 1px solid $red; ;
    border-radius: 6px 6px 0 0;

    .prices__tariff {
      transition: 0.5s;
      font-weight: 600;
      font-size: 24px;
      letter-spacing: normal;
    }

    .prices__price {
      padding-left: 10px;

      transition: 0.5s;
    }

    .prices__tariff-wrapper {
      color: $red;
    }
  }
}

@media ($tablet) {
  .prices {
    margin-bottom: 95px;
    
    .container {
      padding-top: 55px;
      padding-left: 4.4%;
      padding-right: 4.5%;
    }
  }

  .prices__header {
    margin-bottom: 21px;

    font-weight: 600;
    font-size: 30px;
    line-height: 150%;
  }

  .prices__text {
    margin-bottom: 32px;
  }

  .prices__tel {
    margin-bottom: 28px;
  }

  .prices__wrapper {
    padding: 0 5.2%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  .prices__buy {
    position: absolute;
    bottom: 0;
    left: 0;

    width: 100%;
    margin-left: 0;
  }

  .prices__card {
    position: relative;
    width: 46%;
    padding: 30px 3% 50px;
    margin-right: 20px;
    margin-top: 2px;

    &:last-child {
      margin-right: 0;
    }

    &:hover {
      padding-top: 28px;
      padding-left: calc(3% - 2px);
      padding-right: calc(3% - 2px);
      border: 3px solid $red;
      border-bottom: 1px solid $red;;
      border-radius: 6px 6px 0 0;
  
      .prices__tariff {
        font-weight: 600;
        font-size: 24px;
        letter-spacing: normal;
      }
  
      .prices__price {
        padding-left: 10px;
      }
    }
  }
}

@media ($desktop) {
  .prices {
    margin-bottom: 107px;
    
    .container {
      display: flex;
      align-items: center;
      padding-left: 19.5%;
      padding-right: 19.3%;
    }
  }

  .prices__text-wrapper {
    margin-top: 17px;
    order: 1;
    width: 293.77px;
  }

  .prices__wrapper {
    padding: 0;
    width: 65.6%;
    margin-right: auto;
  }

  .prices__card {
    width: 370px;
    margin-right: 5px;
  }

  .prices__header {
    margin-top: 0;
    margin-bottom: 15px;

    font-weight: 600;
    font-size: 55px;
    line-height: 90px;
    text-align: left;
  }

  .prices__contact {
    margin-bottom: 13px;
    padding-left: 10px;

    font-weight: 500;
    font-size: 22px;
    line-height: 34px;
  }

  .prices__tel {
    padding-left: 7px;

    font-size: 20px;
    line-height: 32px;
    text-decoration: none;

    color: black;
  }

  .prices__text {
    text-align: left;
    padding-top: 30px;
    margin-bottom: 60px;
  }

  .prices__tariff-wrapper {
    display: flex;
    flex-wrap: wrap;
  }

  .prices__tariff {
    margin-bottom: 26px;

    margin-right: auto;
    font-weight: 600;
    font-size: 28px;
    line-height: 38px;
    letter-spacing: -1.27273px;
  }

  .prices__price {
    margin: 0;
    margin-top: 5px;
    font-weight: bold;
    font-size: 25px;
    line-height: 34px;
  }

  .prices__desc {
    margin-bottom: 28px;
    padding-right: 22px;

    text-align: left;
  }

  .prices__list {
    padding-top: 31px;
    margin-bottom: 0;
  }

  .prices__item {
    margin-bottom: 15px;
    padding-right: 10px;

    font-size: 16px;
    line-height: 32px;

    &--top {
      margin-bottom: 7px;
    }
  }

  .prices__buy {
    left: 50%;
    bottom: 31px;
    transform: translate(-50%);

    padding-top: 31px;
    width: 310px;
    height: 75px;

    font-weight: bold;
    font-size: 12px;
    line-height: 15px;
    letter-spacing: 2px;
  }

  .prices__card {
    padding: 37px 28px 133px;
    
    &:hover {
      padding-top: 35px;
      padding-left: 26px;
      padding-right: 26px;
      padding-bottom: 131px;
      border: 3px solid $red;
      border-bottom: 3px solid $red;;
      border-radius: 6px;
  
      .prices__tariff {
        letter-spacing: normal;
        font-weight: 600;
        font-size: 28px;
        line-height: 38px;
      }
  
      .prices__price {
        padding-left: 10px;
      }

      .prices__buy {
        bottom: 29px;
      }
    }
  }
}